.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  &__header {
    height: 4rem;
    background-color: black;
  }

  &__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__footer {
    height: 8rem;
    background-color: black;
  }
}
